:root {
  /* variables */
  --spacing: 1rem;
  --main-color: lightblue;

  background-image: url(https://news.harvard.edu/wp-content/uploads/2024/08/Lightning2500.jpg?w=1680&h=945&crop=1);
  background-color: darkred;
  background-size: cover;
  background-repeat: no-repeat;

  /* debugging */
  * {
    /* outline: solid red 1px; */
  }
}

body {
  background-color: var(--main-color);
  font-family: Arial, Helvetica, sans-serif;

  min-width: 57.5rem;
  max-width: 57.5rem;
  min-height: 100vh;
  margin: auto;

  display: grid;
  grid-template-rows: auto auto 1fr;
}

#title-section {
  padding-bottom: var(--spacing);

  h1 {
    font-size: 2.5rem;
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
  #subtitle-section {
    font-size: 1.5rem;
    /* margin-left: 2rem; */
    text-align: center;
  }
}

/* poorly named */
#weather-results-section {
  padding: var(--spacing);
  width: 100%;
  height: 41rem;

  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100%;
  background-repeat: no-repeat;

  display: grid;
  grid-template-columns: 52% 45%;
  justify-content: space-between;

  #weather-results-left-section {
    /* background-color: red; */

    #weather-report-form {
      padding: var(--spacing);
      max-height: min-content;

      background-color: lightblue;
      border: white 10px solid;
      border-radius: var(--spacing);
      box-shadow: 5px 5px 5px black;

      display: flex;
      flex-direction: column;

      .form-line {
        display: grid;
        grid-template-columns: 1fr auto;
        gap: var(--spacing);
      }

      #unit-input-form-section {
        display: inline;

        max-width: fit-content;
      }

      input#location-input {
        display: inline;
        padding: 0 calc(0.25 * var(--spacing));

        border: gray solid 2px;
        border-radius: 0.25rem;

        &:hover,
        &:focus {
          appearance: none;
          /* add to reset.css? */
          outline: none;

          border: black solid 2px;
        }
      }

      button {
        display: block;
        padding: calc(0.25 * var(--spacing));

        margin-top: calc(0.5 * var(--spacing));

        border-radius: calc(0.5 * var(--spacing));

        background-color: white;

        &:hover {
          transform: scale(1.01);
          cursor: pointer;
        }
      }
    }

    #demo-btns-section {
      #demo-btns-title {
        margin-top: calc(0.5 * var(--spacing));

        color: yellow;
        font-size: xx-large;
        font-weight: bold;
        text-align: center;
        text-decoration: underline;
      }

      #demo-btns-container {
        margin-top: calc(0.25 * var(--spacing));

        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        column-gap: calc(0.5 * var(--spacing));
        row-gap: calc(0.5 * var(--spacing));
        .demo-btn {
          padding: calc(0.1 * var(--spacing)) calc(0.5 * var(--spacing));

          border-radius: calc(0.5 * var(--spacing));
          background-color: white;

          border: none;

          &:hover {
            cursor: pointer;
            background-color: var(--main-color);
            transform: scale(1.1);
          }
        }
      }
    }
  }

  #weather-card {
    padding: var(--spacing);
    margin-left: auto;
    min-height: 39rem;
    max-height: 39rem;

    background-color: white;
    border: lightblue 10px solid;
    border-radius: var(--spacing);
    box-shadow: 5px 5px 5px black;

    display: grid;
    grid-template-rows: auto auto 1fr auto;

    #weather-info {
      #report-address {
        text-align: center;
        font-size: large;
        font-weight: bold;
      }

      #condition-report {
        text-align: center;
      }

      #temp-report {
        text-align: center;
      }

      #weather-emoji {
        font-size: 9rem;
        text-align: center;
      }
    }

    .spacer {
      display: grid;
      align-content: center;

      padding: calc(0.5 * var(--spacing)) 0;

      .spacer-line {
        height: 0.5px;
        background-color: black;
      }
    }

    #mason-quote-container {
      #mason-quote {
        font-style: italic;
        text-align: center;
      }
      #mason-quote-src {
        text-align: center;
      }
    }

    #disclaimers {
      font-size: smaller;
    }
  }
}

#credits {
  text-align: center;
  font-size: smaller;
}
